const searchData = (
  objList: TableCandidatesInterface[],
  text: string
): TableCandidatesInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (
        prop === "names" ||
        prop === "n_id" ||
        prop === "phone_number" ||
        prop === "main_doc_title" ||
        prop === "title" ||
        prop === "description" ||
        prop === "validation_status"
      ) {
        if (product[prop]) {
          flag =
            product[prop]!.toString()
              .toLowerCase()
              .indexOf(text.toLowerCase()) > -1;
        }
      }
      if (flag) break;
    }
    return flag;
  });
};

export enum DocumentValidationStatus {
  Waiting = "waiting",
  Approved = "approved",
  Rejected = "rejected",
}

interface DocumentValidationToPrintInterface {
  // DATA FROM MAIN PROPERTY
  no: number;
  names: string;
  national_id: string;
  phone: string;
  email: string;
  document: string;
  comment: string;
  status: DocumentValidationStatus;
}

enum DocumentValidationReportContentKeyInterface {
  applicant_name = "names",
  national_id = "national_id",
  phone_number = "phone",
  email = "email",
  document = "document",
  comment = "comment",
  status = "status",
}

interface DocumentValidationContentInterface {
  key: DocumentValidationReportContentKeyInterface;
  title: string;
  status: boolean;
}

const defaultReportContent: DocumentValidationContentInterface[] = [
  {
    key: DocumentValidationReportContentKeyInterface.applicant_name,
    title: "Applicant name",
    status: true,
  },
  {
    key: DocumentValidationReportContentKeyInterface.national_id,
    title: "National ID",
    status: true,
  },
  {
    key: DocumentValidationReportContentKeyInterface.email,
    title: "Email",
    status: true,
  },
  {
    key: DocumentValidationReportContentKeyInterface.phone_number,
    title: "Phone",
    status: true,
  },
  {
    key: DocumentValidationReportContentKeyInterface.document,
    title: "Document title",
    status: true,
  },
  {
    key: DocumentValidationReportContentKeyInterface.comment,
    title: "Validation comment",
    status: true,
  },
  {
    key: DocumentValidationReportContentKeyInterface.status,
    title: "Status",
    status: true,
  },
];

interface DocumentValidationProps {
  //   REDUX ############################
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  education_categories: education_category[] | null;
  sessions: SessionPlanInterface | "none" | null;

  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  loadEducationCategories: (callback: (loading: boolean) => void) => void;
  getCandidatesInInstitute: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface DocumentValidationState {
  loading: boolean;
  loadingCandidates: boolean;
  thisEducation: education_category | null;
  selectAll: boolean;
  reportMainData: TableCandidatesInterface[] | null;
  reportContentExport: DocumentValidationContentInterface[];
  reportTitle: string;
  dataToPrint: DocumentValidationToPrintInterface[] | null;
  sortProperty: string;
  isSorted: boolean;
  sortOrder: "ASC" | "DESC";
  searchInput: string;
  searchPositionInput: string;
  generateReportModal: {
    reportType: "EXCEL" | "PDF";
    status: boolean;
  } | null;
  formError: {
    element: string;
    msg: string;
  } | null;
  selectedStatus: DocumentValidationStatus | "all";
  selectPosition: boolean;
  selectedPosition: PositionAPI | null;
  downloading: boolean;
}

export class _DocumentValidation extends Component<
  DocumentValidationProps,
  DocumentValidationState
> {
  constructor(props: DocumentValidationProps) {
    super(props);

    this.state = {
      loading: true,
      loadingCandidates: true,
      selectedStatus: "all",
      selectAll: true,
      thisEducation: null,
      reportMainData: null,
      reportContentExport: defaultReportContent,
      reportTitle: "Document validation report",
      dataToPrint: null,
      sortProperty: "",
      isSorted: false,
      sortOrder: "ASC",
      searchInput: "",
      searchPositionInput: "",
      generateReportModal: null,
      formError: null,
      selectPosition: false,
      selectedPosition: null,
      downloading: false,
    };
  }

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };

  setLoadingCandidates = (status: boolean) => {
    this.setState({ loadingCandidates: status });
  };

  setSelectAll = (status: boolean) => {
    this.setState({ selectAll: status });
  };

  setSelectPosition = (status: boolean) => {
    this.setState({ selectPosition: status });
  };

  setSelectedPosition = (thisPosition: PositionAPI | null) => {
    this.setState({ selectedPosition: thisPosition });
  };

  setReportTitle = (thisTitle: string) => {
    this.setState({ reportTitle: thisTitle });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  setSortOrder = (thisOrder: "ASC" | "DESC") => {
    this.setState({ sortOrder: thisOrder });
  };

  setSortProperty = (thisProperty: string) => {
    this.setState({ sortProperty: thisProperty });
  };

  setIsSorted = (status: boolean) => {
    this.setState({ isSorted: status });
  };

  setGenerateReportModal = (
    data: {
      reportType: "EXCEL" | "PDF";
      status: boolean;
    } | null
  ) => {
    this.setState({ generateReportModal: data });
  };

  setDownloading = (value: boolean) => {
    this.setState({ downloading: value });
  };

  setSort = (thisProperty: string) => {
    if (
      this.state.sortProperty === thisProperty &&
      this.state.sortOrder === "ASC"
    ) {
      this.setSortOrder("DESC");
    } else {
      this.setSortOrder("ASC");
    }
    this.setSortProperty(thisProperty);
    this.setIsSorted(true);
  };

  loadCandidatesInInstitute = (
    status: boolean,
    education_categories_data?: education_category[] | null
  ) => {
    // LOADING CANDIDATES BY EDICATION CATEGORY ===========================
    if (
      !status &&
      education_categories_data &&
      education_categories_data.length > 0 &&
      this.state.thisEducation === null
    ) {
      this.setState({ thisEducation: education_categories_data[0] });
      if (
        (this.props.documentReducer.candidatesInInstitute == null ||
          this.props.documentReducer.candidatesInInstitute.length <= 0) &&
        this.props.auth.application_session &&
        this.props.auth.application_session.session_plan_id
      ) {
        this.props.getCandidatesInInstitute(
          education_categories_data[0].education_institute_id,
          this.props.auth.application_session.session_plan_id,
          (
            status: boolean,
            msg: string,
            data?: CandidatesInterface[] | null
          ) => {
            this.setLoadingCandidates(status);
            if (!status && msg.length > 0) {
              console.error({ loadingCandidatesError: msg });
              this.props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
              this.setLoadingCandidates(false);
            }
            if (!status && data) {
              this.setState({
                reportMainData: DocumentInArray(
                  this.props.documentReducer,
                  data,
                  "all"
                ),
              });
            }
          }
        );
      } else {
        this.setLoadingCandidates(false);
      }
    }
  };

  componentDidMount = () => {
    this.setLoading(false);
    // LOADING DOCUMENT CATEGORIES ===============================
    if (
      this.props.documentReducer.documentCategory === null ||
      this.props.documentReducer.documentCategory.length <= 0
    ) {
      this.props.getDocumentCategory();
    }

    // LOADING DOCUMETN SUB CATEGORYIES =========================
    if (
      this.props.documentReducer.documentSubCategory === null ||
      this.props.documentReducer.documentSubCategory.length <= 0
    ) {
      this.props.getDocumentSubCategory();
    }

    // LOADING DOCUMETN FIELDS =========================
    if (
      this.props.documentReducer.documentFields === null ||
      this.props.documentReducer.documentFields.length <= 0
    ) {
      this.props.getDocumentFields();
    }

    // LOADING EDICATION CATEGORY =========================================
    if (
      this.props.education_categories === null ||
      this.props.education_categories.length <= 0
    ) {
      this.props.loadEducationCategories(this.loadCandidatesInInstitute);
    } else if (this.state.thisEducation === null) {
      this.loadCandidatesInInstitute(false, this.props.education_categories);
    }
  };

  FC_GeneratePDF = () => {
    if (!document.querySelectorAll("#report_content tbody tr").length) {
      this.props.PushPopupAlert({
        title: `Can't generate Report of empty Data`,
        theme: PopupAlertTheme.warning,
      });
      return false;
    }

    if (this.props.auth.application_session) {
      const table: HTMLElement | null =
        document.getElementById("report_content");
      const reportSettings: reportSettingsInterface = {
        doneBy: this.props.auth.user.fname + " " + this.props.auth.user.lname,
        title: "Document Validation Report",
        subjectTitle: `Document validation ${
          this.props.sessions && this.props.sessions !== "none"
            ? this.props.sessions.session_plan_name
            : "All session"
        }`,
        subject: "All applications",
        period:
          this.props.sessions && this.props.sessions !== "none"
            ? this.props.sessions.session_plan_name
            : "All session",
        footer: "Online Recruitment",
        institute: this.state.thisEducation
          ? this.state.thisEducation.education_category_title
          : "All institute",
        orientation: "l",
      };
      table && generatePDF(reportSettings, table, this.setDownloading);
    }
  };

  updateContentToPrint = () => {
    // UPDATE REPORT TITLE ========================
    if (this.props.auth.application_session) {
      let title =
        "Application marks publication Report - " +
        this.props.auth.application_session.session_plan_name;
      this.setReportTitle(title);
    }
    if (
      !this.state.reportMainData &&
      this.props.documentReducer.candidatesInInstitute
    ) {
      this.setState({
        reportMainData: DocumentInArray(
          this.props.documentReducer,
          this.props.documentReducer.candidatesInInstitute,
          "all"
        ),
      });
    }
    // UPDATE CONTENT =============================
    if (this.state.reportMainData) {
      let arrayNew: DocumentValidationToPrintInterface[] = [];
      let n: number = 1;
      for (const key in this.state.reportMainData) {
        if (
          Object.prototype.hasOwnProperty.call(this.state.reportMainData, key)
        ) {
          let dataArray: DocumentValidationToPrintInterface | null = null;
          dataArray = {
            no: n,
            names: this.state.reportMainData![key].names,
            national_id: this.state.reportMainData![key].n_id,
            phone: this.state.reportMainData![key].phone_number,
            email: this.state.reportMainData![key].email,
            document: this.state.reportMainData![key].main_doc_title,
            comment: this.state.reportMainData![key].validated_by
              ? this.state.reportMainData![key].validated_by!.comment
              : "",
            status: this.state.reportMainData![key]
              .validation_status as DocumentValidationStatus,
          };

          for (const key in this.state.reportContentExport) {
            if (
              Object.prototype.hasOwnProperty.call(
                this.state.reportContentExport,
                key
              )
            ) {
              if (!this.state.reportContentExport[key].status && dataArray) {
                delete dataArray[this.state.reportContentExport[key].key];
              }
            }
          }
          dataArray && arrayNew.push(dataArray);
          n++;
        }
      }
      this.setState({ dataToPrint: arrayNew });
      arrayNew = [];
      n = 1;
    }
  };

  updateReportContent = (key: DocumentValidationReportContentKeyInterface) => {
    let index: number = this.state.reportContentExport.findIndex(
      (item) => item.key === key
    );
    let newArray: DocumentValidationContentInterface[] =
      this.state.reportContentExport;
    newArray[index].status = !newArray[index].status;
    this.setState({ reportContentExport: newArray });
    let isSelectAllNeeded = newArray.find((item) => item.status === false);
    if (isSelectAllNeeded) {
      this.setSelectAll(false);
    } else {
      this.setSelectAll(true);
    }

    // UPDATE APPLICATION LIST FOR UPDATE ==============================
    this.updateContentToPrint();
  };

  isChangeAllowed = (
    key: DocumentValidationReportContentKeyInterface
  ): boolean => {
    if (
      key === DocumentValidationReportContentKeyInterface.applicant_name ||
      key === DocumentValidationReportContentKeyInterface.document ||
      key === DocumentValidationReportContentKeyInterface.national_id
    ) {
      return false;
    }
    return true;
  };

  resetReportContent = (currentState: boolean) => {
    let newArray: DocumentValidationContentInterface[] =
      this.state.reportContentExport;
    for (const key in newArray) {
      if (this.isChangeAllowed(newArray[key].key)) {
        newArray[key].status = !currentState;
      }
    }

    this.setState({ reportContentExport: newArray });
    this.setSelectAll(!currentState);
    this.updateContentToPrint();
  };

  FC_FilterReportData = (
    allData: TableCandidatesInterface[],
    status: DocumentValidationStatus | "all"
  ): TableCandidatesInterface[] => {
    let result: TableCandidatesInterface[] = allData;
    if (status !== "all") {
      result = result.filter((item) => item.validation_status === status);
    }

    return result;
  };

  ReportStatuses = (
    allData: TableCandidatesInterface[]
  ): {
    title: string;
    count: number;
    status: DocumentValidationStatus | "all";
  }[] => {
    let output: {
      title: string;
      count: number;
      status: DocumentValidationStatus | "all";
    }[] = [{ title: "All", count: allData.length, status: "all" }];
    output.push(
      {
        title: capitalizeFirstLetter(
          DocumentValidationStatus.Waiting.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.validation_status === DocumentValidationStatus.Waiting
        ).length,
        status: DocumentValidationStatus.Waiting,
      },
      {
        title: capitalizeFirstLetter(
          DocumentValidationStatus.Approved.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.validation_status === DocumentValidationStatus.Approved
        ).length,
        status: DocumentValidationStatus.Approved,
      },
      {
        title: capitalizeFirstLetter(
          DocumentValidationStatus.Rejected.toLocaleLowerCase()
        ),
        count: allData.filter(
          (itm) => itm.validation_status === DocumentValidationStatus.Rejected
        ).length,
        status: DocumentValidationStatus.Rejected,
      }
    );
    return output;
  };

  render() {
    if (this.state.loading) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading data...
        </div>
      );
    }

    if (this.props.auth.application_session === null) {
      return <NoSession />;
    }

    return (
      <div>
        <SideNavigation>
          {/* <div className="">
            <div className="text-gray-600 text-sm mb-2">Session:</div>
            <div
              onClick={() => this.setSelectSession(true)}
              className="font-bold bg-blue-50 border border-blue-200 rounded py-2 cursor-pointer text-xs px-2 hover:underline justify-between text-blue-600  mb-4 w-full flex flex-row items-center animate__animated animate__zoomIn"
            >
              {this.props.auth.application_session !== null
                ? `${this.props.auth.application_session.session_plan_name}`
                : "Filter by session"}
              <FiEdit className="text-lg text-blue-600" />
            </div>
          </div> */}

          <div className="pt-1">
            <div className="text-base font-semibold text-gray-700 pb-2 pt-2">
              Filter by statuses:
            </div>
            {this.props.documentReducer.candidatesInInstitute === null ||
            this.state.loadingCandidates ? (
              <Loading />
            ) : (
              this.ReportStatuses(
                DocumentInArray(
                  this.props.documentReducer,
                  this.props.documentReducer.candidatesInInstitute,
                  "all"
                )
              ).map(
                (
                  itm: {
                    title: string;
                    count: number;
                    status: DocumentValidationStatus | "all";
                  },
                  i: number
                ) => (
                  // itm.count > 0 &&
                  <Fragment key={itm.title}>
                    <div
                      onClick={() =>
                        this.setState({ selectedStatus: itm.status })
                      }
                    >
                      <MainItem
                        title={itm.title}
                        count={itm.count}
                        active={
                          this.state.selectedStatus === itm.status
                            ? true
                            : false
                        }
                      />
                    </div>
                  </Fragment>
                )
              )
            )}
          </div>
        </SideNavigation>

        {/* BODY CONTENT--------------------------------- */}
        <section>
          {/* REPORT TITLE ============================================ */}
          <div className="grid md:grid-cols-12">
            <div className="col-span-8">
              <div className="mt-4 mx-4 flex flex-col gap-1">
                <span className="text-3xl font-bold text-gray-700">
                  Document validation report
                </span>
                {/* <div
                  onClick={() => this.setState({ selectPosition: true })}
                  className="flex flex-row gap-2 items-end"
                >
                  <span className="text-gray-500 text-xl">Position:</span>
                  <span className="text-blue-600 font-bold underline cursor-pointer text-xl">
                    {this.state.selectedPosition !== null
                      ? this.state.selectedPosition.position_title
                      : "All position"}
                  </span>
                </div> */}
              </div>
            </div>
            <div className="col-span-4">
              <div className="float-right mt-2">
                {this.props.documentReducer.candidatesInInstitute &&
                  this.props.documentReducer.candidatesInInstitute.length >
                    0 && (
                    <div className="flex flex-row items-center justify-end font-bold mb-2">
                      <BiExport className="text-2xl text-gray-500" />
                      <span className="text-gray-500">Export as</span>
                      <button
                        className="px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200"
                        disabled={
                          this.props.documentReducer.candidatesInInstitute &&
                          !this.state.downloading
                            ? false
                            : true
                        }
                        onClick={() => {
                          this.updateContentToPrint();
                          this.setGenerateReportModal({
                            reportType: "EXCEL",
                            status: true,
                          });
                        }}
                      >
                        EXCEL
                      </button>
                      <button
                        className={`px-4 py-1 ml-1 rounded bg-white border hover:text-blue-600 font-bold hover:bg-blue-50 border-gray-200 ${
                          this.state.downloading && "cursor-wait"
                        }`}
                        onClick={() => this.FC_GeneratePDF()}
                        disabled={
                          this.props.documentReducer.candidatesInInstitute
                            ? false
                            : true
                        }
                      >
                        PDF
                      </button>
                    </div>
                  )}
              </div>
            </div>
          </div>
          {/* END REPORT TITLE ============================================ */}

          {/* ######################################################## */}
          <div className="bg-white my-4">
            <div className="w-full px-2 py-2">
              <input
                onChange={(e) => this.setState({ searchInput: e.target.value })}
                type="text"
                value={this.state.searchInput}
                placeholder="Search..."
                className="w-full bg-gray-100 py-2 px-3 rounded focus:border-gray-800"
                disabled={
                  this.state.loadingCandidates === null ||
                  this.props.documentReducer.candidatesInInstitute === null ||
                  this.FC_FilterReportData(
                    DocumentInArray(
                      this.props.documentReducer,
                      this.props.documentReducer.candidatesInInstitute,
                      "all"
                    ),
                    this.state.selectedStatus
                  ).length <= 0
                }
              />
            </div>
            <table className="w-full shadow" id="report_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("names")}
                  >
                    <div className="flex items-center gap-1">
                      Names <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("national_id")}
                  >
                    <div className="flex items-center gap-1">
                      National Id <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("phone")}
                  >
                    <div className="flex items-center gap-1">
                      Phone <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("email")}
                  >
                    <div className="flex items-center gap-1">
                      Email <BiSort />
                    </div>
                  </td>
                  {/* <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("gender")}
                  >
                    <div className="flex items-center gap-1">
                      Gender <BiSort />
                    </div>
                  </td> */}
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("position_name")}
                  >
                    <div className="flex items-center gap-1">
                      Position <BiSort />
                    </div>
                  </td>
                  <td
                    className="px-2 py-2 cursor-pointer"
                    onClick={() => this.setSort("status")}
                  >
                    <div className="flex items-center gap-1">
                      Status <BiSort />
                    </div>
                  </td>
                </tr>
              </thead>
              <tbody>
                {this.props.documentReducer.candidatesInInstitute === null ||
                this.state.loadingCandidates ? (
                  <TableLoading cols={8} />
                ) : (
                  searchData(
                    this.FC_FilterReportData(
                      DocumentInArray(
                        this.props.documentReducer,
                        this.props.documentReducer.candidatesInInstitute,
                        "all"
                      ),
                      this.state.selectedStatus
                    ),
                    this.state.searchInput
                  ).map((item: TableCandidatesInterface, i) => (
                    <tr
                      key={i + item.user_id}
                      className={`hover:bg-blue-100 hover:text-blue-700 border-b hover:border-blue-500 text-sm bg-white ${
                        item.validation_status !== "rejected" &&
                        item.validation_status !== "approved" &&
                        "border-gray-500"
                      }`}
                    >
                      <td className="px-2 py-2">{i + 1}</td>
                      <td className="px-2 py-2">{item.names}</td>
                      <td className="px-2 py-2">{item.n_id}</td>
                      <td className="px-2 py-2">{item.phone_number}</td>
                      <td className="px-2 py-2">{item.email}</td>
                      {/* <td className="px-2 py-2">{item.title}</td> */}
                      <td className="px-2 py-2">{item.main_doc_title}</td>
                      {/* <td className="px-2 py-2">{item.marks}</td> */}
                      <td
                        className={`px-2 py-2 ${
                          item.validation_status === "rejected"
                            ? "text-yellow-700"
                            : item.validation_status === "approved"
                            ? "text-blue-700"
                            : ""
                        }`}
                      >
                        {capitalizeFirstLetter(item.validation_status)}
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </section>
        {/* END BODY CONTENT--------------------------------- */}

        {/* ---------------------------------------------------------------------------- */}

        {/* GENERATE REPORT MODAL CONTENT MODAL */}
        {this.state.generateReportModal !== null &&
          this.state.generateReportModal.status === true && (
            <Modal
              title={
                <div className="flex flex-row gap-4 items-center">
                  {/* <IoBriefcase className="text-5xl text-gray-500" /> */}
                  <div className="flex flex-col">
                    <span className="font-bold text-2xl text-gray-500">
                      Generating Report contents
                    </span>
                    <span className="text-sm text-blue-600 font-normal">
                      Generating {this.state.generateReportModal.reportType}{" "}
                      Report
                    </span>
                  </div>
                </div>
              }
              backDrop={true}
              theme={Themes.default}
              close={() => this.setGenerateReportModal(null)}
              backDropClose={false}
              widthSizeClass={ModalSize.medium}
              displayClose={true}
              padding={{ title: true }}
            >
              <div className="px-6">
                <div className="my-2 text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col">
                    <label>Title</label>
                    <input
                      type="text"
                      className={`text-left input-white w-20 inline-block ${
                        this.state.formError &&
                        this.state.formError.element === "report_title"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder="Sub Category Title"
                      value={this.state.reportTitle}
                      onChange={(e) => {
                        this.setFormError(null);
                        this.setReportTitle(e.target.value);
                      }}
                      disabled={this.state.reportMainData ? false : true}
                    />
                    {this.state.formError &&
                      this.state.formError.element === "report_title" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                  <div className="flex flex-col mt-2">
                    <label>Report content</label>
                    <div className="grid grid-cols-2 md:grid-cols-3 items-center gap-2 mt-2">
                      {this.state.reportContentExport.map((item, index) => (
                        <div
                          key={index}
                          className={`flex items-center gap-1 ${
                            this.isChangeAllowed(item.key)
                              ? "cursor-pointer"
                              : "cursor-not-allowed"
                          }`}
                          onClick={() =>
                            this.isChangeAllowed(item.key) &&
                            this.updateReportContent(item.key)
                          }
                          title={
                            !this.isChangeAllowed(item.key)
                              ? "Required for every report!"
                              : ""
                          }
                        >
                          {item.status ? (
                            <MdCheckBox className="w-6 h-6 text-blue-700" />
                          ) : (
                            <MdCheckBoxOutlineBlank className="w-6 h-6" />
                          )}
                          <span>{item.title}</span>
                        </div>
                      ))}
                    </div>
                    {this.state.formError &&
                      this.state.formError.element === "report_content" && (
                        <span className="text-red-500">
                          {this.state.formError.msg}
                        </span>
                      )}
                  </div>
                </div>
                <div className="flex justify-between mb-2 mt-4">
                  <div
                    className={`flex items-center gap-1 cursor-pointer`}
                    onClick={() =>
                      this.resetReportContent(this.state.selectAll)
                    }
                  >
                    {this.state.selectAll ? (
                      <MdCheckBox className="w-6 h-6 text-blue-700" />
                    ) : (
                      <MdCheckBoxOutlineBlank className="w-6 h-6" />
                    )}
                    <span>Select all</span>
                  </div>

                  {this.state.dataToPrint && this.state.reportMainData && (
                    <ExportToExcel
                      fileData={this.state.dataToPrint}
                      fileName={this.state.reportTitle}
                      btnName="Generate"
                    />
                  )}
                </div>
              </div>
            </Modal>
          )}
        {/* END GENERATE REPORT MODAL CONTENT MODAL */}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  document,
  schoolLevels,
}: StoreState): {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  education_categories: education_category[] | null;
} => {
  return {
    auth: auth,
    documentReducer: document,
    education_categories: schoolLevels.education_categories,
  };
};

export const DocumentValidation = connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  loadEducationCategories,
  getCandidatesInInstitute,
  PushPopupAlert,
})(_DocumentValidation);
